* {
    box-sizing: border-box;
}

body {
    margin: 0px;
    background-color: #F9F9F9;
}

button:focus {
    outline: none;
}

a {
    color: #363636;
    font-size: 1rem;
}

.person-png, .heart-png, .search-png, .arrows-png {
    position: relative;
    top: 5px;
    margin-left: 3px;
}

.special-topBar-wrapper {
    align-items: center;
    display: flex;
}

@media only screen and (max-width: 1700px) {
    .top-menu-special-text {
        display: none!important;
    }
    .top-menu-small-icon {
        margin-right: 25px!important;
