    --breakpoint-desktop: $breakpoint;
    --breakpoint-desktop-navbar: $breakpoint;
    --breakpoint-desktop-small: $breakpoint;
    --breakpoint-desktop-medium: $breakpoint;
    --breakpoint-desktop-large: $breakpoint;
    --breakpoint-desktop-large-gallery-dfp: $breakpoint;
    --breakpoint-desktop-extra-small: $breakpoint;
    --breakpoint-tablet: $breakpoint;
    --breakpoint-mobile: $breakpoint;
    --breakpoint-laptop-height: $breakpoint;
    --breakpoint-laptop-height-medium: $breakpoint;
    --breakpoint-laptop-height-small: $breakpoint;
    --breakpoint-mobile-medium: $breakpoint;
    --breakpoint-mobile-small: $breakpoint;
