    color: #363636;
    direction: rtl;
    -webkit-tap-highlight-color: transparent;
    text-align: right;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 1vw;
    font: inherit;
    vertical-align: baseline;
    box-sizing: inherit;
    display: flex;
    position: relative;
    z-index: 800;
    height: 70px;
    margin: 0 20px;
    background-color: #fff;
    border-bottom: 1px solid #eee;
}

.normal-topBar-wrapper .dropbtn:hover, .normal-topBar-wrapper.sub-menu li:hover {
    background-color: rgba(211, 211, 211, 0.3);
    cursor: pointer;
}


ul li ul {
    position: absolute;
    transition: all 0.5s ease;
    margin-top: 1rem;
    background-color: #FFFFFF;
    background-size: initial;
}

ul li ul.dropdown {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    transition: all 0.5s ease;
    margin-top: 1rem;
    background-color: #FFFFFF;
    background-size: initial;
}

.from-year-to-year-li {
    padding: 0px;
